import {
  guestOrder,
  GuestOrderInput,
  guestOrderByToken,
} from '@/order-confirmation/api';
import {
  OrderSearchFields,
  OrderConfirmation as Component,
} from '@/order-confirmation/components';
import {
  Actions,
  Alert,
  AlertCode,
  AlertType,
  orderConfirmationReducer,
} from '@/order-confirmation/reducers/orderConfirmation';
import { useText } from '@adobe/elsie/i18n';
import { Container } from '@adobe/elsie/lib';
import {
  HTMLAttributes,
  useCallback,
  useEffect,
  useReducer,
} from 'preact/compat';

export interface OrderConfirmationProps extends HTMLAttributes<HTMLDivElement> {
  token?: string;
  routeHome?: () => string;
  routeSupport?: () => string;
}

function useOrderConfirmation(token?: string) {
  const [state, dispatch] = useReducer(orderConfirmationReducer, {
    isLoading: false,
    token,
    details: null,
  });

  const translations = useText({
    invalidOrder: 'OrderConfirmation.Errors.invalidOrder',
    invalidSearch: 'OrderConfirmation.Errors.invalidSearch',
    pageNotFound: 'OrderConfirmation.Errors.pageNotFound',
    unknown: 'OrderConfirmation.Errors.unknown',
  });

  const buildAlert = useCallback(
    (message: string): Alert | undefined => {
      switch (message) {
        case 'Invalid order':
        case "We couldn't locate an order with the information provided.":
          return {
            type: AlertType.WARNING,
            code: AlertCode.INVALID_ORDER,
            message: translations.invalidOrder,
          };
        case 'Invalid search':
          return {
            type: AlertType.WARNING,
            code: AlertCode.INVALID_SEARCH,
            message: translations.invalidSearch,
          };
      }
    },
    [translations.invalidOrder, translations.invalidSearch]
  );

  const fetchOrderDetails = useCallback(
    (fields?: OrderSearchFields) => {
      guestOrder(fields as GuestOrderInput)
        .then((details) => {
          // order number is only returned if valid order inputs are provided
          const hasOrderDetails = !!details?.number;

          const alert = hasOrderDetails
            ? undefined
            : buildAlert('Invalid search');

          dispatch({
            type: Actions.SET_DETAILS,
            details,
            alert,
          });
        })
        .catch(() => {
          const alert = buildAlert('Invalid search');

          dispatch({
            type: Actions.SET_DETAILS,
            details: null,
            alert,
          });
        });
    },
    [buildAlert]
  );

  const fetchOrderDetailsByToken = useCallback(
    (token: string) => {
      guestOrderByToken(token)
        .then((details) => {
          // order number is only returned if valid order inputs are provided
          const hasOrderDetails = !!details?.number;

          const alert = hasOrderDetails
            ? undefined
            : buildAlert('Invalid order');

          dispatch({
            type: Actions.SET_DETAILS,
            details,
            alert,
          });
        })
        .catch((error) => {
          const knownAlert = buildAlert(error.message);

          if (!knownAlert) {
            console.error(error);
            return;
          }

          dispatch({
            type: Actions.ALERT,
            alert: knownAlert,
          });
        });
    },
    [buildAlert]
  );

  const dismissAlert = () => {
    dispatch({ type: Actions.DISMISS_ALERT });
  };

  const handleOrderSearch = function (fields: OrderSearchFields) {
    fetchOrderDetails(fields);
  };

  useEffect(() => {
    if (!state.token) return;

    dispatch({ type: Actions.LOADING });

    fetchOrderDetailsByToken(state.token);
  }, [state.token, fetchOrderDetailsByToken]);

  return {
    state,
    handleOrderSearch,
    dismissAlert,
  };
}

export const OrderConfirmation: Container<OrderConfirmationProps> = ({
  token,
  routeHome,
  routeSupport,
}) => {
  const { state, handleOrderSearch, dismissAlert } =
    useOrderConfirmation(token);

  return (
    <Component
      state={state}
      dismissAlert={dismissAlert}
      onOrderSearch={handleOrderSearch}
      routeHome={routeHome}
      routeSupport={routeSupport}
    />
  );
};
