import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { Text } from '@adobe/elsie/i18n';
import { Card, Divider } from '@adobe/elsie/components';
import '@/order-confirmation/components/CartSummary/CartSummary.css';
import { CartSummarySkeleton } from '@/order-confirmation/components/CartSummary';
import { CartSummaryItem } from '@/order-confirmation/components/CartSummaryItem';
import { transformItems } from '@/order-confirmation/data/transforms/transform-order-items';
import { State } from '@/order-confirmation/reducers';

// TODO details type
export interface CartSummaryProps extends HTMLAttributes<HTMLDivElement> {
  isLoading: boolean;
  details: State['details'];
}

export const CartSummary: FunctionComponent<CartSummaryProps> = ({
  isLoading,
  details,
}) => {
  if (isLoading) {
    return <CartSummarySkeleton />;
  }

  // TODO tax included
  const taxIncluded = false;
  const items = details!.items!;

  // const totalQuantity = details?.items?.reduce(
  //   (total: number, item: any) => total + item.quantity_ordered,
  //   0
  // );

  // TODO: Quantity should be based on config: https://jira.corp.adobe.com/browse/USF-1089
  const totalQuantity = items.length;

  return (
    <div className="order-confirmation-cart-summary">
      <Card variant="secondary">
        <div className="order-confirmation-cart-summary__heading">
          <h2 className={'order-confirmation-cart-summary__title'}>
            <Text id="OrderConfirmation.CartSummary.title" /> ({totalQuantity})
          </h2>
        </div>
        <Divider
          className="order-confirmation-cart-summary__divider-top"
          variant="primary"
        />

        <div className="order-confirmation-cart-summary__items">
          {items?.map((item: any) => {
            const cartItem = transformItems(item);
            return (
              <CartSummaryItem
                key={cartItem?.uid}
                item={cartItem}
                taxIncluded={taxIncluded}
              />
            );
          })}
        </div>
      </Card>
    </div>
  );
};
