import { FunctionComponent } from 'preact';
import { Skeleton, SkeletonRow } from '@adobe/elsie/components';
import '@/order-confirmation/components/StaticSkeleton/StaticSkeleton.css';
import { HTMLAttributes } from 'preact/compat';

export interface StaticSkeletonProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'size'> {
  rows: number;
  size?: 'xsmall' | 'small' | 'medium' | 'large' | 'xlarge';
}
export const StaticSkeleton: FunctionComponent<StaticSkeletonProps> = ({
  rows,
  size = 'xsmall',
  ...props
}) => {
  return (
    <Skeleton className={'static-skeleton'} {...props}>
      {Array.from(Array(rows).keys()).map((i) => (
        <SkeletonRow key={i} variant={'heading'} size={size} fullWidth={true} />
      ))}
    </Skeleton>
  );
};
