import { GuestOrderDataFragment } from '@/order-confirmation/__generated__/types';

type NonNullableItems = NonNullable<GuestOrderDataFragment['items']>;
type NonNullableItem = NonNullable<NonNullableItems[number]>;

export function transformItems(item: NonNullableItem) {
  return {
    uid: item.id,
    quantity: item.quantity_ordered,
    sku: item.product!.sku,
    name: item.product!.name,
    image: {
      src: item.product!.thumbnail!.url,
      alt: item.product!.thumbnail!.label,
    },
    price: {
      value: item.product_sale_price.value,
      currency: item.product_sale_price.currency,
    },
    priceInclTax: {
      value: item.product_sale_price.value!,
      currency: item.product_sale_price.currency!,
    },
    total: {
      value: item.product_sale_price.value! * item.quantity_ordered!,
      currency: item.product_sale_price.currency,
    },
    totalInclTax: {
      value: item.product_sale_price.value! * item.quantity_ordered!,
      currency: item.product_sale_price.currency,
    },
    discount: {
      value: 0,
      currency: item.product_sale_price.currency,
    },
    regularPrice: {
      value: item.product!.price_range.maximum_price!.regular_price.value!,
      currency:
        item.product!.price_range.maximum_price!.regular_price.currency!,
    },
    discounted:
      item.product!.price_range.maximum_price!.regular_price.value! *
        item.quantity_ordered! !==
      item.product_sale_price.value! * item.quantity_ordered!,
    configurableOptions: transformConfigurableOptions(item),
    senderName:
      item.__typename === 'GiftCardOrderItem'
        ? item.gift_card!.sender_name
        : undefined,
    senderEmail:
      item.__typename === 'GiftCardOrderItem'
        ? item.gift_card!.sender_email!
        : undefined,
    recipientEmail:
      item.__typename === 'GiftCardOrderItem'
        ? item.gift_card!.recipient_email!
        : undefined,
    recipientName:
      item.__typename === 'GiftCardOrderItem'
        ? item.gift_card!.recipient_name
        : undefined,
  };
}
function transformConfigurableOptions(item: NonNullableItem) {
  if (!('selected_options' in item)) return;

  const configurableOptions: Record<string, string> = {};

  for (const option of item.selected_options!) {
    configurableOptions[option!.label] = option!.value;
  }

  return configurableOptions;
}
