import { HTMLAttributes } from 'preact/compat';
import { FunctionComponent, VNode } from 'preact';
import { classes } from '@adobe/elsie/lib';
import '@/order-confirmation/components/OrderConfirmation/OrderConfirmation.css';
import {
  CartSummary,
  Details,
  Footer,
  Header,
  OrderSearchFields,
  OrderSearchForm,
  OrderSummary,
  Summary,
} from '@/order-confirmation/components';
import {
  AlertCode,
  State,
} from '@/order-confirmation/reducers/orderConfirmation';
import { useBreakpoint } from '@/order-confirmation/hooks/useBreakpoint';

const ConditionalWrapper: FunctionComponent<{
  children?: VNode;
  className: string;
}> = ({ children, className }) => {
  const breakpoint = useBreakpoint();
  return breakpoint !== 'small' ? (
    <div className={className}>{children}</div>
  ) : (
    <>{children}</>
  );
};

export interface OrderConfirmationProps extends HTMLAttributes<HTMLDivElement> {
  state: State;
  dismissAlert: () => void;
  onOrderSearch: (fields: OrderSearchFields) => void;
  routeHome?: () => string;
  routeSupport?: () => string;
}

// TODO types
function mapDetailsToSummary(orderDetails: any): Summary | undefined {
  const total = orderDetails?.total;

  if (!total) {
    return undefined;
  }

  const grandTotal = total?.grand_total;
  const subtotal = total?.subtotal;
  const shipping = total?.total_shipping;
  const tax = total?.total_tax;

  return {
    total: {
      inclTax: {
        amount: grandTotal?.value || 0,
        currency: grandTotal?.currency || '',
      },
    },
    subtotal: {
      amount: subtotal?.value || 0,
      currency: subtotal?.currency || '',
      isTaxIncl: false,
    },
    shipping: {
      amount: shipping?.value || 0,
      currency: shipping?.currency || '',
    },
    tax: {
      amount: tax?.value || 0,
      currency: tax?.currency || '',
    },
  };
}

type AsideProps = {
  state: State;
};

const Aside: FunctionComponent<AsideProps> = ({ state }) => {
  const { isLoading, details } = state;
  const summary = mapDetailsToSummary(details);

  return (
    <>
      <OrderSummary isLoading={isLoading} summary={summary} />
      <CartSummary isLoading={isLoading} details={details} />
    </>
  );
};

const AsideElement: FunctionComponent<AsideProps> = ({ state }) => {
  return (
    <ConditionalWrapper className="order-confirmation__aside">
      <Aside state={state} />
    </ConditionalWrapper>
  );
};

const Main: FunctionComponent<OrderConfirmationProps> = ({
  state,
  routeHome,
  routeSupport,
}) => {
  const { details, isLoading } = state;

  const billingAddress = details?.billing_address;
  const customerName = billingAddress?.firstname || '';
  const orderNumber = details?.number || '';
  const orderStatus = details?.status || '';

  const showCustomerHeader = !!customerName && !!orderNumber;

  return (
    <>
      {(isLoading || (!isLoading && showCustomerHeader)) && (
        <Header
          isLoading={isLoading}
          customerName={customerName}
          orderNumber={orderNumber}
          orderStatus={orderStatus}
        />
      )}

      <Details isLoading={isLoading} orderDetails={details} />

      <Footer
        isLoading={isLoading}
        routeHome={routeHome}
        routeSupport={routeSupport}
      />
    </>
  );
};

const MainElement: FunctionComponent<OrderConfirmationProps> = ({
  state,
  routeHome,
  routeSupport,
  dismissAlert,
  onOrderSearch,
}) => {
  return (
    <ConditionalWrapper className="order-confirmation__main">
      <Main
        state={state}
        routeHome={routeHome}
        routeSupport={routeSupport}
        dismissAlert={dismissAlert}
        onOrderSearch={onOrderSearch}
      />
    </ConditionalWrapper>
  );
};

export const OrderConfirmation: FunctionComponent<OrderConfirmationProps> = ({
  className,
  state,
  dismissAlert,
  routeHome,
  routeSupport,
  onOrderSearch,
}) => {
  const { details, alert, isLoading } = state;
  const hasOrderDetails = !!details?.number;
  const isInvalidOrder = alert && alert.code === AlertCode.INVALID_ORDER;
  const isInvalidSearch = alert && alert.code === AlertCode.INVALID_SEARCH;

  if (!isLoading && !hasOrderDetails) {
    return (
      <div className={classes(['order-confirmation', className])}>
        <OrderSearchForm
          alert={
            isInvalidOrder || isInvalidSearch
              ? {
                  heading: alert.message,
                  description: '',
                  type: alert.type,
                  onDismiss: dismissAlert,
                }
              : undefined
          }
          onOrderSearch={onOrderSearch}
        />
      </div>
    );
  }

  return (
    <div className={classes(['order-confirmation', className])}>
      <MainElement
        state={state}
        dismissAlert={dismissAlert}
        routeHome={routeHome}
        routeSupport={routeSupport}
        onOrderSearch={onOrderSearch}
      />
      <AsideElement state={state} />
    </div>
  );
};
