import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import { Button } from '@adobe/elsie/components';
import { Text } from '@adobe/elsie/i18n';
import { FooterSkeleton } from '@/order-confirmation/components';
import '@/order-confirmation/components/Footer/Footer.css';

export interface FooterProps extends HTMLAttributes<HTMLDivElement> {
  isLoading: boolean;
  routeHome?: () => string;
  routeSupport?: () => string;
}

export const Footer: FunctionComponent<FooterProps> = ({
  isLoading,
  className,
  routeHome,
  routeSupport,
  ...props
}) => {
  if (isLoading) {
    return <FooterSkeleton />;
  }

  return (
    <div
      className={classes(['order-confirmation-footer', className])}
      {...props}
    >
      {routeHome?.() && (
        <Button
          data-testid="order-confirmation-footer__continue-button"
          className="order-confirmation-footer__continue-button"
          size={'medium'}
          variant={'primary'}
          key={'continueShopping'}
          type={'submit'}
          href={routeHome?.()}
        >
          <Text id={'OrderConfirmation.Footer.continueShopping'} />
        </Button>
      )}

      <p className="order-confirmation-footer__contact-support">
        <Text id={'OrderConfirmation.Footer.help'} />{' '}
        {routeSupport?.() && (
          <a
            href={routeSupport?.()}
            rel={'noreferrer'}
            className="order-confirmation-footer__contact-support-link"
            data-testid="order-confirmation-footer__contact-support-link"
          >
            <Text id={'OrderConfirmation.Footer.contactSupport'} />
          </a>
        )}
      </p>
    </div>
  );
};
