import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import '@/order-confirmation/components/Details/Details.css';
import { Price, Card } from '@adobe/elsie/components';
import { Text } from '@adobe/elsie/i18n';
import { OrderDetails } from '@/order-confirmation/reducers/orderConfirmation';
import {
  StaticSkeleton,
  DetailsSkeleton,
} from '@/order-confirmation/components';

export interface DetailsProps extends HTMLAttributes<HTMLDivElement> {
  isLoading: boolean;
  orderDetails: OrderDetails | null;
}

export const Details: FunctionComponent<DetailsProps> = ({
  isLoading,
  orderDetails,
}) => {
  if (isLoading) {
    return <DetailsSkeleton />;
  }

  const hasOrderDetails = orderDetails && orderDetails?.number;

  const contactDetails = orderDetails?.email || '';
  const hasToDisplayContactDetails = orderDetails && contactDetails !== '';

  const shippingAddress = orderDetails?.shipping_address;
  const hasToDisplayShippingAddress = orderDetails && shippingAddress !== null;

  const billingAddress = orderDetails?.billing_address;
  const hasToDisplayBillingAddress = orderDetails && billingAddress !== null;

  const shippingMethod = orderDetails?.shipping_method;
  const shippingCost = orderDetails?.total?.total_shipping?.value;
  const shippingCurrency = orderDetails?.total?.total_shipping?.currency;
  const hasToDisplayShippingMethod =
    orderDetails && shippingMethod && shippingCurrency && shippingCost != null;

  const paymentMethods = orderDetails?.payment_methods;
  const hasPaymentMethod = paymentMethods && paymentMethods.length > 0;
  const selectedPaymentMethod = hasPaymentMethod ? paymentMethods[0]?.name : '';
  const hasToDisplayPaymentMethod =
    orderDetails && hasPaymentMethod && selectedPaymentMethod !== '';

  return (
    <Card
      variant={'secondary'}
      className={'order-confirmation-details order-confirmation__card'}
    >
      <div className={'order-confirmation-details__header'}>
        <h2 className={'order-confirmation-details__title'}>
          <Text id={'OrderConfirmation.Details.title'} />
        </h2>
      </div>

      <div className={'order-confirmation-details__contact'}>
        {(!orderDetails || hasToDisplayContactDetails) && (
          <h3>
            <Text id={'OrderConfirmation.Details.contact'} />
          </h3>
        )}

        {hasToDisplayContactDetails && <p>{contactDetails}</p>}

        {!hasOrderDetails && (
          <StaticSkeleton
            rows={1}
            data-testid="order-confirmation-details-contact-skeleton"
          />
        )}
      </div>

      <div className={'order-confirmation-details__information'}>
        <div className={'order-confirmation-details__shipping_address'}>
          {(!hasOrderDetails || hasToDisplayShippingAddress) && (
            <h3>
              <Text id={'OrderConfirmation.Details.shippingAddress'} />
            </h3>
          )}

          {hasToDisplayShippingAddress && (
            <>
              <p>
                {shippingAddress?.firstname} {shippingAddress?.lastname}
              </p>

              {shippingAddress?.street.map((street, index) => (
                <p key={index}>{street}</p>
              ))}

              <p>
                {shippingAddress?.city} {shippingAddress?.region}{' '}
                {shippingAddress?.postcode}, {shippingAddress?.country_code}
              </p>
            </>
          )}

          {!hasOrderDetails && (
            <StaticSkeleton
              rows={3}
              data-testid="order-confirmation-shipping-address-skeleton"
            />
          )}
        </div>

        <div className={'order-confirmation-details__billing_address'}>
          {(!hasOrderDetails || hasToDisplayBillingAddress) && (
            <h3>
              <Text id={'OrderConfirmation.Details.billingAddress'} />
            </h3>
          )}

          {hasToDisplayBillingAddress && (
            <>
              <p>
                {billingAddress?.firstname} {billingAddress?.lastname}
              </p>

              {billingAddress?.street.map((street, index) => (
                <p key={index}>{street}</p>
              ))}

              <p>
                {billingAddress?.city} {billingAddress?.region}{' '}
                {billingAddress?.postcode}, {billingAddress?.country_code}
              </p>
            </>
          )}

          {!hasOrderDetails && (
            <StaticSkeleton
              rows={3}
              data-testid="order-confirmation-billing-address-skeleton"
            />
          )}
        </div>

        <div className={'order-confirmation-details__shipping-method'}>
          {(!hasOrderDetails || hasToDisplayShippingMethod) && (
            <h3>
              <Text id={'OrderConfirmation.Details.shippingMethod'} />
            </h3>
          )}

          {hasToDisplayShippingMethod && (
            <>
              {shippingCost === 0 ? (
                <p className="order-confirmation-order-summary__price">
                  <Text id="OrderConfirmation.Details.freeShipping" />
                </p>
              ) : (
                <p>
                  <Price amount={shippingCost} currency={shippingCurrency} />{' '}
                  {shippingMethod}
                </p>
              )}
            </>
          )}

          {!hasOrderDetails && (
            <StaticSkeleton
              rows={1}
              data-testid="order-confirmation-details-shipping-method-skeleton"
            />
          )}
        </div>

        <div className={'order-confirmation-details__payment-method'}>
          {(!hasOrderDetails || hasToDisplayPaymentMethod) && (
            <h3>
              <Text id={'OrderConfirmation.Details.paymentMethod'} />
            </h3>
          )}

          {hasToDisplayPaymentMethod && <p>{selectedPaymentMethod}</p>}

          {!hasOrderDetails && (
            <StaticSkeleton
              rows={1}
              data-testid="order-confirmation-details-payment-method-skeleton"
            />
          )}
        </div>
      </div>
    </Card>
  );
};
