import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/order-confirmation/components/OrderSummary/OrderSummary.css';
import { Text } from '@adobe/elsie/i18n';
import { Card, Divider, Price } from '@adobe/elsie/components';
import {
  StaticSkeleton,
  OrderSummarySkeleton,
} from '@/order-confirmation/components';

export type CartTaxItem = {
  label: string;
  amount: {
    value?: number;
    currency?: string;
  };
};

export type Summary = {
  total: {
    inclTax: {
      amount: number;
      currency: string;
    };
    exclTax?: {
      amount: number;
      currency: string;
    };
  };
  subtotal: {
    amount: number;
    currency: string;
    isTaxIncl: boolean;
  };
  shipping?: {
    amount: number;
    currency: string;
  };
  tax?: {
    amount: number;
    currency: string;
    breakdown?: CartTaxItem[];
  };
};

export interface OrderSummaryProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'summary'> {
  isLoading: boolean;
  summary?: Summary;
  variant?: 'primary' | 'secondary';
}

export const OrderSummary: FunctionComponent<OrderSummaryProps> = ({
  className,
  isLoading,
  summary,
  variant = 'secondary',
}) => {
  if (isLoading) {
    return <OrderSummarySkeleton />;
  }

  const { total, subtotal, shipping, tax } = summary ?? {};

  return (
    <Card
      className={classes(['order-confirmation-order-summary', className])}
      variant={variant}
    >
      {/* move heading to elsie? */}
      <h2 className="order-confirmation-order-summary__title">
        <Text id="OrderConfirmation.OrderSummary.title" />
      </h2>

      <Divider variant="primary" />

      {subtotal != undefined ? (
        <div
          data-testid="order-summary-subtotal"
          className="order-confirmation-order-summary__row"
        >
          <p>
            <Text id="OrderConfirmation.OrderSummary.subtotal" />
          </p>
          <Price
            amount={subtotal?.amount}
            currency={subtotal?.currency}
            className={classes([
              'order-confirmation-order-summary__price',
              className,
            ])}
          />
        </div>
      ) : (
        <StaticSkeleton
          rows={1}
          data-testid="order-confirmation-order-summary-subtotal-skeleton"
        />
      )}

      {subtotal?.isTaxIncl && (
        <div
          data-testid="order-summary-subtotal-incl-tax"
          className="order-confirmation-order-summary__row order-confirmation-order-summary__row--muted"
        >
          <p>
            <Text id="OrderConfirmation.OrderSummary.includingTaxes" />
          </p>
        </div>
      )}

      {shipping != undefined ? (
        <div
          data-testid="order-summary-shipping"
          className="order-confirmation-order-summary__row"
        >
          <p>
            <Text id="OrderConfirmation.OrderSummary.shipping" />
          </p>

          {shipping.amount === 0 ? (
            <p className="order-confirmation-order-summary__price">
              <Text id="OrderConfirmation.OrderSummary.freeShipping" />
            </p>
          ) : (
            <Price
              amount={shipping.amount}
              currency={shipping.currency}
              className={classes([
                'order-confirmation-order-summary__price',
                className,
              ])}
            />
          )}
        </div>
      ) : (
        <StaticSkeleton
          rows={1}
          data-testid="order-confirmation-order-summary-shipping-skeleton"
        />
      )}

      {tax != undefined && !tax.breakdown && (
        <div
          data-testid="order-summary-tax"
          className="order-confirmation-order-summary__row"
        >
          <p>
            <Text id="OrderConfirmation.OrderSummary.tax" />
          </p>
          <Price
            amount={tax.amount}
            currency={tax.currency}
            className={classes([
              'order-confirmation-order-summary__price',
              className,
            ])}
          />
        </div>
      )}

      {tax != undefined && tax.breakdown && (
        <>
          <Divider variant="secondary" />

          <div className="order-confirmation-order-summary__row order-confirmation-order-summary__row--margin-bottom">
            <p>
              <Text id="OrderConfirmation.OrderSummary.taxBreakdown" />
            </p>
          </div>

          {tax.breakdown.map((tax) => (
            <div
              data-testid="order-summary-tax-breakdown"
              key={tax.label}
              className="order-confirmation-order-summary__row order-confirmation-order-summary__row--muted"
            >
              <p>{tax.label}</p>

              <Price
                amount={tax.amount.value!}
                currency={tax.amount.currency!}
                className={classes([
                  'order-confirmation-order-summary__price',
                  className,
                ])}
              />
            </div>
          ))}

          <div
            data-testid="order-summary-tax-breakdown-total"
            className="order-confirmation-order-summary__row order-confirmation-order-summary__row--strong order-confirmation-order-summary__row--margin-top"
          >
            <p>
              <Text id="OrderConfirmation.OrderSummary.taxTotal" />
            </p>

            <Price
              amount={tax.amount}
              currency={tax.currency}
              className={classes([
                'order-confirmation-order-summary__price',
                className,
              ])}
            />
          </div>

          <Divider variant="secondary" />
        </>
      )}

      {total != undefined ? (
        <div
          data-testid="order-summary-total-incl-tax"
          className="order-confirmation-order-summary__row order-confirmation-order-summary__row--emphasized"
        >
          <p>
            <Text id="OrderConfirmation.OrderSummary.total" />
          </p>

          <Price
            amount={total?.inclTax.amount}
            currency={total?.inclTax.currency}
            className={classes([
              'order-confirmation-order-summary__price',
              className,
            ])}
          />
        </div>
      ) : (
        <StaticSkeleton
          rows={1}
          data-testid="order-confirmation-order-summary-total-skeleton"
        />
      )}

      {total?.exclTax != undefined && (
        <div
          data-testid="order-summary-total-excl-tax"
          className="order-confirmation-order-summary__row order-confirmation-order-summary__row--muted"
        >
          <p>
            <Text id="OrderConfirmation.OrderSummary.totalExclTax" />
          </p>

          <Price
            amount={total.exclTax.amount}
            currency={total.exclTax.currency}
            className={classes([
              'order-confirmation-order-summary__price',
              className,
            ])}
          />
        </div>
      )}
    </Card>
  );
};
