import {
  GuestOrderByTokenQuery,
  GuestOrderQuery,
} from '@/order-confirmation/__generated__/types';

export enum Actions {
  ADD_ORDER_REFERENCE = 'ADD_ORDER_REFERENCE',
  LOADING = 'LOADING',
  SET_DETAILS = 'SET_DETAILS',
  ALERT = 'ALERT',
  DISMISS_ALERT = 'DISMISS_ALERT',
}

export type OrderDetails =
  | GuestOrderQuery['guestOrder']
  | GuestOrderByTokenQuery['guestOrderByToken'];

interface LoadingAction {
  type: Actions.LOADING;
}

interface SetDetailsAction {
  type: Actions.SET_DETAILS;
  details: OrderDetails | null;
  alert?: Alert;
}

export enum AlertType {
  ERROR = 'error',
  WARNING = 'warning',
  SUCCESS = 'success',
}

export enum AlertCode {
  INVALID_ORDER = 'invalid_order',
  INVALID_SEARCH = 'invalid_search',
  UNKNOWN = 'unknown_error',
}

export type Alert = {
  type: AlertType;
  code: AlertCode;
  message: string;
};

interface AlertAction {
  type: Actions.ALERT;
  alert: Alert;
}

interface DismissAlertAction {
  type: Actions.DISMISS_ALERT;
}

export interface State {
  isLoading: boolean;
  details: OrderDetails | null;
  token?: string;
  alert?: Alert;
}

type Action =
  | LoadingAction
  | SetDetailsAction
  | AlertAction
  | DismissAlertAction;

export function orderConfirmationReducer(state: State, action: Action): State {
  switch (action.type) {
    case Actions.LOADING:
      return {
        ...state,
        isLoading: true,
      };
    case Actions.SET_DETAILS:
      return {
        ...state,
        isLoading: false,
        details: action.details,
        alert: action.alert,
      };
    case Actions.ALERT:
      return {
        ...state,
        isLoading: false,
        alert: action.alert,
      };
    case Actions.DISMISS_ALERT:
      return {
        ...state,
        alert: undefined,
      };
    default:
      return state;
  }
}
