import { FunctionComponent } from 'preact';
import { Icon } from '@adobe/elsie/components';
import { OrderSuccess } from '@adobe/elsie/icons';
import { Text } from '@adobe/elsie/i18n';
import { HeaderSkeleton } from '@/order-confirmation/components';
import '@/order-confirmation/components/Header/Header.css';

export interface HeaderProps {
  isLoading: boolean;
  customerName?: string;
  orderNumber?: string;
  orderStatus?: string;
}
export const Header: FunctionComponent<HeaderProps> = ({
  isLoading,
  customerName,
  orderNumber,
  orderStatus,
}) => {
  if (isLoading) {
    return <HeaderSkeleton />;
  }

  return (
    <div className={'order-confirmation-header order-confirmation__card'}>
      <Icon
        source={OrderSuccess}
        size="64"
        className={'order-confirmation-header__icon'}
      />

      <h1 className={'order-confirmation-header__title'}>
        {customerName ? (
          <Text
            id={'OrderConfirmation.Header.title'}
            fields={{ name: customerName }}
          />
        ) : (
          <Text id={'OrderConfirmation.Header.defaultTitle'} />
        )}
      </h1>

      {orderNumber && (
        <p>
          <Text
            id={'OrderConfirmation.Header.order'}
            fields={{ order: orderNumber }}
          />
          {orderStatus !== undefined && orderStatus !== '' && (
            <span className={'order-confirmation-header__status'}>
              {' '}
              ({orderStatus})
            </span>
          )}
        </p>
      )}
    </div>
  );
};
