import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import '@/order-confirmation/components/OrderSearchForm/OrderSearchForm.css';
import { Button, Card, Field, Input } from '@adobe/elsie/components';
import { Text, useText } from '@adobe/elsie/i18n';
import { getFormValues } from '@adobe/elsie/lib';
import {
  InLineAlert,
  InLineAlertProps,
} from '@/order-confirmation/components/InLineAlert';

export interface OrderSearchFields {
  email: string;
  number: string;
  postcode?: string;
}

export interface OrderSearchFormProps extends HTMLAttributes<HTMLDivElement> {
  alert?: InLineAlertProps;
  onOrderSearch?: (fields: OrderSearchFields) => void;
}

export const OrderSearchForm: FunctionComponent<OrderSearchFormProps> = ({
  alert,
  onOrderSearch,
}) => {
  const translations = useText({
    email: 'OrderConfirmation.OrderSearchForm.email',
    postcode: 'OrderConfirmation.OrderSearchForm.postcode',
    number: 'OrderConfirmation.OrderSearchForm.orderNumber',
  });

  const handleSubmit = (event: Event) => {
    event.preventDefault();
    const formValues = getFormValues(event.target as HTMLFormElement);
    onOrderSearch?.(formValues as OrderSearchFields);
  };

  return (
    <Card
      variant={'secondary'}
      className={
        'order-confirmation-order-search-form order-confirmation__card'
      }
    >
      <h2 className={'order-confirmation-order-search-form__title'}>
        <Text id={'OrderConfirmation.OrderSearchForm.title'} />
      </h2>

      <p>
        <Text id={'OrderConfirmation.OrderSearchForm.description'} />
      </p>

      {alert && (
        <InLineAlert
          className={'order-confirmation-order-search-form__alert'}
          data-testid='order-confirmation-alert'
          {...alert}
        />
      )}

      <form
        className={'order-confirmation-order-search-form__fields'}
        onSubmit={handleSubmit}
      >
        <div className={'order-confirmation-order-search-form__fields-columns'}>
          <Field>
            <Input
              id="order-email"
              name="email"
              type={'email'}
              autocomplete={'email'}
              placeholder={translations.email}
              floatingLabel={translations.email}
              required={true}
              aria-label={translations.email}
              aria-required={true}
            />
          </Field>

          <Field>
            <Input
              id="postcode"
              name="postcode"
              type={'text'}
              autocomplete={'postal-code'}
              placeholder={translations.postcode}
              floatingLabel={translations.postcode}
              required={false}
              aria-label={translations.postcode}
              aria-required={true}
            />
          </Field>
        </div>

        <Field>
          <Input
            id="order-number"
            name="number"
            type={'text'}
            placeholder={translations.number}
            floatingLabel={translations.number}
            required={true}
            aria-label={translations.number}
            aria-required={true}
          />
        </Field>

        <Button
          className={'order-confirmation-order-search-form__button'}
          size={'medium'}
          variant={'primary'}
          key={'logIn'}
          type={'submit'}
        >
          <Text id={'OrderConfirmation.OrderSearchForm.button'} />
        </Button>
      </form>
    </Card>
  );
};
